html {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

body {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  margin: 0;
  padding: 0;
}

main {
  font-size: 1.1rem;
}

h1 {
  padding: 0;
  margin: 0 0 8px 0;
}

.bg-light {
  background-color: #f5f5f5;
}

.border-bottom {
  border-bottom: 1px solid #d3d7db;
}

.container-top {
  max-width: 900px;
  justify-content: center;
  margin: 0 auto;
}

.container {
  max-width: 900px;
  justify-content: center;
  margin: 40px auto 140px auto;
  padding: 8px;
}

section {
  width: 100%;
}

hr {
  margin: 40px 0;
}

.py-4 {
  padding: 8px 0 18px 0;
}

code {
  color: red;
  background-color: rgb(233, 233, 233);
  padding: 0 4px;
  border-radius: 4px;
}

.btn-reset,
.btn-check-password {
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  padding: 8px;
  background-color: rgb(50, 122, 230);
  border-radius: 8px;
  color: rgb(255, 255, 255);
  font-size: 20px;
  cursor: pointer;
  border: 1px solid #6c757d;
}

.d-flex {
  display: flex;
}

.pe-20 {
  padding-right: 20px;
}